<template>
  <AppHeader />
  <div class="w-full flex">
    <div class="m-auto">
      <h1 class="text-center mb-7 mt-5 text-3xl text-">Tamara To Do's {{ num }}</h1>
      <div class="mt-5 mb-6">
        <input class="bg-slate-200 rounded-xl pl-2 text-pink-50 hover:bg-pink-400 focus:bg-pink-400" id="in" type="text" v-model="newName">
        <button id="button" class="bg-gradient-to-t  ml-4 rounded-l rounded-r p-2" @click="Create" type="submit"> <span> submit </span> </button>
      </div>
      <div id="ulLists">
      <ul id="append" class="justify-between">
        <li id="listItem" class="flex justify-between mb-5 p-7" v-for="(num, index) in names" v-bind:key="index++">
          {{ index }}. {{ num }}
          <div>
            <button class="hover:bg-red-600 p-1 rounded-lg" @click="Delete(index)">
              <img src="./assets/Delete-Icon-Transparent-Background.png" width="20" alt="delete">
            </button>
            <button class="hover:bg-green-600 p-1 rounded-lg ml-4" @click="Done(index)">
              <img id="del" src="./assets/checkmark-png-5.png"  width="20" alt="done">
            </button>
          </div>
        </li>
      </ul>
      </div>
    </div>
  </div>
</template>

<script>
import AppHeader from './components/AppHeader.vue';
import $ from 'jquery'

// localStorage.clear();

export default {
  components: {
    AppHeader
  },
  data(){
    return {
      newName: "",
      names : [],
    }
  },
  created () {
    if (localStorage.getItem("Storage_key") !== null){
      this.names = JSON.parse(localStorage.getItem("Storage_key"));
    }
    else {
      this.names = [];
    }
  },
  computed: {
    num(){
      return this.names.length;
    },
  },
  methods: {
    randomM(){
      return Math.random();
    },
    Create(){
      if (this.newName == ""){
        alert("Please enter a name");
        return;
      }
      this.names.push(this.newName);
      this.newName = "";
      setTimeout(() => {
        localStorage.setItem("Storage_key", JSON.stringify(this.names)) ;
      }, 500);
    },
    Delete(index){
      index--;
      //add class to an item
      $("li")[index].classList.add("delete");
      setTimeout(() => {
        this.names.splice(index, 1);
        localStorage.setItem("Storage_key", JSON.stringify(this.names)) ;
      }, 1000);
    },
    Done (index) {
      index--;
      $("li")[index].style.textDecoration = "line-through";
      $("li")[index].style.opacity = "0.5";
      $("li")[index].classList.add("disable");
    },
  }
}

</script>

